.navbar1 {
    /* border: 1px solid red; */
    width: 50%;
    display: flex;
    justify-content: space-around;
}

.navbar1 a {
    text-decoration: none;
    font-size: 24px;
}

.navbar1 a:hover {
    color: aqua;
}

.active {
    color: aqua
}

.unactive {
    color :#C4C4C4;
}

.navContainer {
    width: 100%;
    height: 80px;
    margin: auto;
    background-color: #02171f;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    justify-content: space-between;
    box-shadow: rgba(0, 0, 0, 0.6) 0px 30px 90px;
}

.sec1, .sec2, .sec3, .sec4, .sec5 {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    width: 100%;
    font-size: 16px;
    color : rgb(159, 125, 187);
    /* border: 2px solid white */
}

.name{
    width: 350px;
}
.name a{
    text-decoration: none;
    
}
.name h2{
    color: aqua;
    font-size: 30px;
    font-stretch: ultra-expanded;
}

.sec1{
    margin-top: -82px;
}




